import { useState, useEffect } from 'react'
import { Button } from 'reactstrap'
import { Link, NavLink } from 'react-router-dom'
import errorImg from '@src/assets/images/pages/error.svg'
import themeConfig from '@configs/themeConfig'

import '@styles/base/pages/page-misc.scss'
import { isUserLoggedIn } from '@utils'

const Error = () => {

  const [userData, setUserData] = useState(null)
  let taksLink = '/apps/todo/'

  useEffect(() => {
    if (isUserLoggedIn() !== null) {
      setUserData(JSON.parse(localStorage.getItem('userData')))
    }
  }, [])

  if (userData) {
    if (userData.role === 'client') {
      taksLink = '/apps/task/list/'
    } else if (userData.role === 'user') {
      taksLink = '/apps/post-task/list'
    }
  } else {
    taksLink = '/login'
  }
  return (
    <div className='misc-wrapper'>
      <NavLink to='/' className='navbar-brand'>
        <span className='brand-logo'>
          <img src={themeConfig.app.appLogoImage} alt='logo' />
          <h2 className='brand-text mb-0'>{themeConfig.app.appName}</h2>
        </span>
      </NavLink>
      <div className='misc-inner p-2 p-sm-3'>
        <div className='w-100 text-center'>
          <h2 className='mb-1'>Page Not Found ğŸ•µğŸ»â€â™€ï¸</h2>
          <p className='mb-2'>Oops! ğŸ˜– The requested URL was not found on this server.</p>
          <Button tag={Link} to={taksLink} color='primary' className='btn-sm-block mb-2'>
            Back to home
          </Button>
          <img className='img-fluid' src={errorImg} alt='Not authorized page' />
        </div>
      </div>
    </div>
  )
}
export default Error
